"use client";
import { useState, useEffect, useCallback } from "react";
import { useSession } from "@/lib/auth-client";
import { Button } from "@/components/ui/button";
import TechStackGrid from "@/components/tech-stack/tech-stack-grid";
import { AddTechForm } from "@/components/tech-stack/add-tech-form";
import { type Tech } from "@/components/tech-stack/tech-stack-grid";
import { toast } from "sonner";
import Image from "next/image";

export interface UserStack {
  id: number;
  name: string;
  technologies: Tech[];
  createdAt?: string;
  updatedAt?: string;
}

export default function Dashboard() {
  const { data: sessionData, isPending } = useSession();
  const [userStacks, setUserStacks] = useState<UserStack[]>([]);
  const [isLoadingInitialData, setIsLoadingInitialData] = useState(true);
  const [newStackName, setNewStackName] = useState("");
  const [addFormOpenStackId, setAddFormOpenStackId] = useState<number | null>(
    null
  );
  const [reorderModeStackId, setReorderModeStackId] = useState<number | null>(
    null
  );

  const hydrateTechnologies = useCallback((rawTechs: any[]): Tech[] => {
    if (!rawTechs) return [];
    return rawTechs.map((rawTech, index) => ({
      id: String(rawTech.id),
      name: rawTech.name,
      color: rawTech.color,
      icon: null, // à adapter si tu veux les icônes
      technologyId: rawTech.technologyId,
      category: rawTech.category,
      gridSpan: rawTech.gridSpan,
      order: rawTech.order !== undefined ? rawTech.order : index,
      isProject: rawTech.isProject,
      favicon: rawTech.favicon,
      url: rawTech.url,
      description: rawTech.description,
    }));
  }, []);

  useEffect(() => {
    const fetchUserStacks = async () => {
      if (sessionData?.user?.id) {
        setIsLoadingInitialData(true);
        try {
          const response = await fetch("/api/tech/stack");
          if (!response.ok) throw new Error("Network or server error");
          const stacksData: UserStack[] = await response.json();
          const hydratedStacks = stacksData.map((stack) => ({
            ...stack,
            technologies: hydrateTechnologies(stack.technologies || []),
          }));
          setUserStacks(hydratedStacks);
        } catch (error) {
          toast.error("Unable to load your stacks.");
          setUserStacks([]);
        } finally {
          setIsLoadingInitialData(false);
        }
      } else if (!isPending && !sessionData) {
        setIsLoadingInitialData(false);
        setUserStacks([]);
      }
    };
    if (!isPending) {
      fetchUserStacks();
    }
  }, [isPending, sessionData, hydrateTechnologies]);

  const handleCreateNewStack = async () => {
    if (!sessionData?.user?.id) {
      toast.error("You must be logged in to create a stack.");
      return;
    }
    if (!newStackName.trim()) return;
    try {
      const response = await fetch("/api/tech/stack", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          name: newStackName.trim(),
          technologies: [],
        }),
      });
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || "Error while creating the stack");
      }
      const newStackData = await response.json();
      const hydratedNewStack: UserStack = {
        ...newStackData,
        technologies: hydrateTechnologies(newStackData.technologies || []),
      };
      setUserStacks((prevStacks) => [...prevStacks, hydratedNewStack]);
      setNewStackName("");
      toast.success(`Stack '${hydratedNewStack.name}' créée !`);
    } catch (error) {
      toast.error((error as Error).message || "Unable to create the stack.");
    }
  };

  // Ajout d'une technologie à une stack
  const handleAddTechToStack = (stackId: number, tech: Tech) => {
    setUserStacks((prevStacks) =>
      prevStacks.map((stack) =>
        stack.id === stackId
          ? { ...stack, technologies: [...stack.technologies, tech] }
          : stack
      )
    );
    setAddFormOpenStackId(null);
  };

  // Suppression d'une techno d'une stack
  const handleRemoveTechFromStack = (stackId: number, techId: string) => {
    setUserStacks((prevStacks) =>
      prevStacks.map((stack) =>
        stack.id === stackId
          ? {
              ...stack,
              technologies: stack.technologies.filter((t) => t.id !== techId),
            }
          : stack
      )
    );
  };

  // Mise à jour d'une techno d'une stack (resize, etc)
  const handleUpdateTechInStack = (
    stackId: number,
    techId: string,
    updates: Partial<Tech>
  ) => {
    setUserStacks((prevStacks) =>
      prevStacks.map((stack) =>
        stack.id === stackId
          ? {
              ...stack,
              technologies: stack.technologies.map((t) =>
                t.id === techId ? { ...t, ...updates } : t
              ),
            }
          : stack
      )
    );
  };

  // Réorganisation des technologies d'une stack
  const handleReorderTechsInStack = (
    stackId: number,
    reorderedTechs: Tech[]
  ) => {
    setUserStacks((prevStacks) =>
      prevStacks.map((stack) =>
        stack.id === stackId
          ? { ...stack, technologies: reorderedTechs }
          : stack
      )
    );
    setReorderModeStackId(null);
  };

  if (isPending || isLoadingInitialData) {
    return (
      <div className="flex justify-center items-center min-h-screen bg-[var(--background)] text-[var(--foreground)]">
        Chargement du dashboard…
      </div>
    );
  }

  if (!sessionData?.user?.id) {
    return (
      <div className="flex flex-col justify-center items-center min-h-screen bg-[var(--background)] text-[var(--foreground)]">
        <p className="text-xl mb-4">
          Connecte-toi pour accéder à ton dashboard.
        </p>
        <Button
          onClick={() => (window.location.href = "/auth/signin")}
          className="bg-[var(--primary)] text-[var(--primary-foreground)] hover:bg-[var(--primary)]/90"
        >
          Se connecter
        </Button>
      </div>
    );
  }

  return (
    <div className="flex flex-col items-center max-w-3xl mx-auto py-8 gap-8">
      {/* Photo de profil centrée */}
      <div className="flex flex-col items-center">
        {sessionData.user.image && (
          <Image
            src={sessionData.user.image}
            alt={sessionData.user.name || "User avatar"}
            width={128}
            height={128}
            className="rounded-full object-cover border-4 border-[var(--primary)]"
          />
        )}
      </div>

      {/* Création de stack très discrète */}
      <div className="w-full flex justify-center">
        <input
          type="text"
          value={newStackName}
          onChange={(e) => setNewStackName(e.target.value)}
          placeholder="Nom de la nouvelle Bento Grid..."
          className="p-2 rounded bg-[var(--input)] border border-[var(--border)] text-sm mr-2 w-64"
        />
        <Button size="sm" variant="outline" onClick={handleCreateNewStack}>
          +
        </Button>
      </div>

      {/* Affichage de toutes les Bento Grids */}
      <div className="w-full flex flex-col gap-8">
        {userStacks.length === 0 && (
          <div className="text-center text-muted-foreground">
            Aucune Bento Grid pour l'instant. Crée ta première !
          </div>
        )}
        {userStacks.map((stack) => {
          const isReorder = reorderModeStackId === stack.id;
          return (
            <div
              key={stack.id}
              className="bg-card rounded-lg border border-border p-6 shadow-lg"
            >
              <div className="flex justify-between items-center mb-4">
                <h2 className="text-xl font-bold">{stack.name}</h2>
                <div className="flex gap-2">
                  {isReorder ? (
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => setReorderModeStackId(null)}
                    >
                      Done
                    </Button>
                  ) : (
                    <>
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => setAddFormOpenStackId(stack.id)}
                        disabled={isReorder}
                      >
                        Ajouter
                      </Button>
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => setReorderModeStackId(stack.id)}
                        disabled={addFormOpenStackId !== null}
                      >
                        Reorder
                      </Button>
                    </>
                  )}
                </div>
              </div>
              {/* Affichage de la grid avec ou sans mode reorder */}
              <TechStackGrid
                technologies={stack.technologies}
                onRemoveTech={
                  isReorder
                    ? (id) => handleRemoveTechFromStack(stack.id, id)
                    : undefined
                }
                onUpdateTech={
                  isReorder
                    ? (id, updates) =>
                        handleUpdateTechInStack(stack.id, id, updates)
                    : undefined
                }
                onReorderTechs={
                  isReorder
                    ? (reordered) =>
                        handleReorderTechsInStack(stack.id, reordered)
                    : undefined
                }
                readOnly={!isReorder}
                showHeader={false}
              />
            </div>
          );
        })}
      </div>
    </div>
  );
}
